<template>
  <div :style="{backgroundColor: bgColor}" class="tab-layout">
    <div class="tab-bar-wrapper">
      <div class="button-bar" :style="{minWidth: barWidth + 'px'}">
        <router-link v-for="(link, index) in links" :key="index" :to="link.to"
          class="button font-regular" @click.native="onClick(index)"
          v-text="link.text">
        </router-link>
      </div>
    </div>
    <!-- sub routes -->
    <router-view v-if="useContentSlot == 'true'"></router-view>
    <!-- content slot -->
    <slot></slot>
  </div>
</template>
<style lang="scss" scoped>
  @import '../../../../scss/variables';
  
  .tab-layout {
    position: fixed; 
    top: 50px; 
    left: 0; 
    z-index: 3; 
    width: 100%;
    
    .tab-bar-wrapper {
      overflow: hidden;

      .button-bar {
        min-width: 270px;
        text-align: center;
        float: left;

        .button {
          height: 46px;
          min-height: 46px;
          line-height: 46px;
          font-size: 13px;

          &.router-link-active {
            background-color: white;
            // color: white;
          }
        }
      }
    }
  }
  
</style>
<script>
  export default {
    props: {
      links: Array,
      barWidth: {
        type: Number,
        required: true
      },
      // page content bg color
      bgColor: {
        type: String,
        default: '#f5f5f5'
      },

      onInit: Function,
      onTabClick: Function,
      useContentSlot: {
        type: String,
        default: 'false'
      }
    },

    mounted () {
      if (this.onInit) this.onInit()
    },

    methods: {
      onClick (index) {
        if (this.onTabClick) this.onTabClick(index)
      }
    }
  }
</script>



