<!-- 我的收徒：奖励、徒弟/徒孙列表 -->
<template>
  <div class="page" v-nav="{hideNavbar: true}">
    <!-- 页头 -->
    <AnimateHeader
      bg-color="#44B549"
      menu-bg-color="#3DA742"
      color="#FFFFFF"
      title="我的收徒"
      :animate="false"
      :on-back-button-click="back"
    >
    </AnimateHeader>

    <tab-layout style="position: fixed; top: 50px; left: 0; z-index: 3; width: 100%;" :links="tabLinks" :bar-width="barWidth" :on-init="fetchData" :on-tab-click="onTabClick" bg-color="#fff"></tab-layout>

    <div class="page-content">

      <!-- 奖励 -->
      <keep-alive>
        <income v-if="loaded && activeIndex == 0" :total-income="totalIncome" :no-data="noIncomeData" :dayly-income="daylyIncome"></income>
      </keep-alive>

      <!-- 徒弟 -->
      <keep-alive>
        <list v-if="loaded && activeIndex == 1" :type="shoutuType" :apprentices="apprentices" :show-content="showContent"
          :offset="offset" :no-more-data="noMoreData" :no-more-data-tips="noMoreDataTips()" :refresh="refreshPrenticeList"
          :infinite="infinite" :today-new-num="today_apprentice_num"></list>
      </keep-alive>

      <!-- 徒孙 -->
      <keep-alive>
        <list v-if="loaded && activeIndex == 2" :type="shoutuType" :apprentices="apprenticesGrandson" :show-content="showContentGrandson"
          :offset="offset" :no-more-data="noMoreGrandsonData" :no-more-data-tips="noMoreGrandsonDataTips()" :refresh="refreshPrenticeGrandsonList"
          :infinite="infiniteGrandson" :today-new-num="today_grandson_num"></list>
      </keep-alive>

    </div>
  </div>
</template>

<style lang="scss" scoped>
  .page, .page-content {
    background-color: #FFF;
  }

  .page-content {
    margin-top: 100px;
  }
</style>

<script>
  import AnimateHeader from '@/widget/animate-header/Index.vue'
  import TabLayout from './layout/TabLayout'
  import Income from './Income'
  import List from './List'

  import { KEY_SHOUTU_TYPE } from 'constants'
  const PAGE_SIZE = 25

  let shoutuType = $storage.get(KEY_SHOUTU_TYPE)

  let tabLinks = [
    {
      to: { path: '/apprentice/achievements/income' },
      text: '奖励'
    },
    {
      to: { path: '/apprentice/achievements/list/1' },
      text: '徒弟'
    }
  ]

  if (shoutuType && shoutuType == 2) { // eslint-disable-line
    tabLinks.push({
      to: { path: '/apprentice/achievements/list/2' },
      text: '徒孙'
    })
  }

  export default {
    components: {
      AnimateHeader,
      TabLayout,
      Income,
      List
    },

    data () {
      return {
        tabLinks: tabLinks,
        barWidth: 270,
        activeIndex: 0,
        loaded: true,
        // income
        totalIncome: null,
        noIncomeData: false,
        daylyIncome: [],
        apprentices: [],
        apprenticesGrandson: [],
        showContent: false,
        showContentGrandson: false,
        offset: undefined,
        grandsonOffset: undefined,
        noMoreData: true,
        noMoreGrandsonData: true,
        shoutuType: '',
        today_apprentice_num: 0,
        today_grandson_num: 0
      }
    },

    mounted () {
      if (this.$route.params.type === '1') {
        this.activeIndex = 1
        this.shoutuType = 1
      }
      if (this.$route.params.type === '2') {
        this.activeIndex = 2
        this.shoutuType = 2
      }
    },

    methods: {
      fetchData () {
        $loading.show('加载中')
        $api.apprenticeListAll(PAGE_SIZE)
          .then(res => res.data && res.data.payload)
          .then((payload) => {
            // 奖励
            this.daylyIncome = payload.daily.length ? payload.daily : [{day: moment().format('YYYY[.]MM[.]DD'), income: 0}] // 默认显示今天的日期和0收入
            this.totalIncome = payload.income
            if (!this.totalIncome) {
              this.noIncomeData = true
            }

            // 徒弟、徒孙
            let apprentices = payload.apprentices_tudi
            let apprenticesGrandson = payload.apprentices_tusun
            let offset
            let grandsonOffset

            _.each(apprentices, (a, i) => {
              a.timestamp = a.create_time.substr(0, 10).replace(/-/g, '.') + ' 加入钱咖'
              if (i === apprentices.length - 1) {
                offset = a.user_id
                this.offset = offset
              }
            })
            _.each(apprenticesGrandson, (a, i) => {
              a.timestamp = a.create_time.substr(0, 10).replace(/-/g, '.') + ' 加入钱咖'
              if (i === apprenticesGrandson.length - 1) {
                grandsonOffset = a.user_id
                this.grandsonOffset = grandsonOffset
              }
            })
            // 我的徒弟
            this.apprentices = payload.apprentices_tudi
            this.noMoreData = (payload.apprentices_tudi.length === 0) || (payload.apprentices_tudi.length < PAGE_SIZE)
            this.showContent = true
            // 我的徒孙
            this.apprenticesGrandson = payload.apprentices_tusun
            this.noMoreGrandsonData = (payload.apprentices_tusun.length === 0) || (payload.apprentices_tusun.length < PAGE_SIZE)
            this.showContentGrandson = true

            this.today_apprentice_num = payload.today_apprentice_num // 今日新增徒弟
            this.today_grandson_num = payload.today_grandson_num // 今日新增徒孙

            this.loaded = true
            $loading.hide()
          })
      },

      infinite (done) {
        if (this.offset === undefined || this.noMoreData) return

        this
          ._list(1, this.offset)
          .then(result => {
            this.apprentices = this.apprentices.concat(result)
            this.noMoreData = result.length === 0 || result.length < PAGE_SIZE
            done()
          })
      },

      infiniteGrandson (done) {
        if (this.grandsonOffset === undefined || this.noMoreGrandsonData) return

        this
          ._list(2, this.grandsonOffset)
          .then(result => {
            this.apprenticesGrandson = this.apprenticesGrandson.concat(result)
            this.noMoreGrandsonData = result.length === 0 || result.length < PAGE_SIZE
            done()
          })
      },

      _list (type, offset) {
        return $api
          .apprenticeList(type, PAGE_SIZE, offset)
          .then(res => res.data && res.data.payload)
          .then(payload => {
            let apprentices = payload.apprentices
            let offset
            let grandsonOffset

            _.each(apprentices, (a, i) => {
              a.timestamp = a.create_time.substr(0, 10).replace(/-/g, '.') + ' 加入钱咖'
              if (i === apprentices.length - 1) {
                offset = a.user_id
                grandsonOffset = a.user_id
              }
            })
            if (type === 1) {
              this.offset = offset
            }
            if (type === 2) {
              this.grandsonOffset = grandsonOffset
            }
            return apprentices
          })
      },

      onTabClick (index) {
        this.activeIndex = index
        this.shoutuType = index
      },

      noMoreDataTips () {
        let len = this.apprentices.length
        return len > 20 ? '共 ' + len + ' 位' + '徒弟' : ''
      },

      noMoreGrandsonDataTips () {
        let len = this.apprenticesGrandson.length
        return len > 20 ? '共 ' + len + ' 位' + '徒孙' : ''
      },

      back () {
        $router.back('/apprentice')
      }
    }
  }
</script>



